@tailwind base;
@tailwind components;
@tailwind utilities;



*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins" , sans-serif;
  

}

body{
  overflow-x: hidden;
}

@layer utilities {
  .animation {
     animation: scroll 10s linear infinite; 
  }
}

@keyframes scroll {

  50% {
    transform: translateX(25%);
    transition-timing-function: cubic-bezier(0, 0.5, 0, 1);  
  }
  
  100% {
    transform: translateX(-20%);
    transition-timing-function: ease-out; 
  }

}

@layer components {
  .tag {
    @apply inline-flex items-center px-3 py-1 m-2 text-sm font-medium bg-gray-200 rounded shadow-md;
  }
}